"use strict";
/**
 * 对外暴露的
 * @class RouteData
 */
Object.defineProperty(exports, "__esModule", { value: true });
class RouteData {
    /**
     *
     * @param state
     */
    static state2RouteData(state) {
        if (!state) {
            return null;
        }
        const recognizeResult = state.recognizeResult || {
            queryData: {},
            pathData: {},
            route: null,
            url: '',
        };
        return {
            id: state.routerURL.id,
            fullPath: state.routerURL.stringifyWithoutSystemData(),
            routeData: Object.assign({}, recognizeResult.pathData, recognizeResult.queryData),
            sessionData: state.data,
            queryData: recognizeResult.queryData,
            pathData: recognizeResult.pathData,
            routeConfig: recognizeResult.route,
            otherData: state.otherData,
        };
    }
}
exports.RouteData = RouteData;



